import React, { Component } from "react";
import { ToastContainer } from "react-toastify";
import config from "../config/config.json";
import http from "../utils/httpService";
import image from './logo192.png';
import "react-toastify/dist/ReactToastify.css";

class Profile extends Component {
  state = {
    posts: [],
  };

   handleFetch=async()=> {
    console.clear();
    const { data: posts } = await http.get(config.urlEndpoint);
    this.setState({ posts });
  }

  handleAdd = async () => {
    const obj = {
      title: "learning react",
      body: "React is easy to understand",
    };
    const { data: post } = await http.post(config.urlEndpoint, obj);
    console.log(post);
    const posts = [post, ...this.state.posts];
    this.setState({ posts });
  };

  handleUpdate = async (post) => {
    post.title = "Updated title";
    const { data } = await http.put(config.urlEndpoint + "/" + post.id, post);

    const posts = [...this.state.posts];
    const index = posts.indexOf(post);
    console.log(posts[0]);
    posts[index] = { ...post };
    this.setState({ posts });
  };

  handleDelete = async (post) => {
    try {
      const temp = await http.delete(
        "https://jsonplaceholder.typicode.com/pos" + "/" + post.id
      );
    } catch (err) {
    }

    const posts = this.state.posts.filter((p) => p.id !== post.id);
    this.setState({ posts });
  };

  render() {
    return (
      <div className="container">
        <ToastContainer />

        <div className="row" style={{ textAlign: "center" }}>
          <div className="col-xs-5 col-xs-offset-3">
            <img src={image} alt="logo" />
            <h1 style={{ fontWeight: "bold" }}>Cipher School</h1>
            <h2>Learning React from 8 - 10 AM</h2>
            <button className="btn btn-success" onClick={this.handleFetch}>
              Fetch Table
            </button>
          </div>
        </div>
        {(()=>{
            if(this.state.posts.length!==0){
                return(
            <>
        <button className="btn btn-primary" onClick={this.handleAdd}>
        {" "}
        Add
      </button>
      <table className="table">
        <thead>
          <tr>
            <th>Title</th>
            <th>Update</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          {this.state.posts.map((post) => (
            <tr key={post.id}>
              <td>{post.title}</td>
              <td>
                <button
                  className="btn btn-info btn-sm"
                  onClick={() => this.handleUpdate(post)}
                >
                  Update
                </button>
              </td>
              <td>
                <button
                  className="btn btn-danger btn-sm"
                  onClick={() => this.handleDelete(post)}
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table> 
      </>       
                )
        }})()}
      </div>
    );
  }
}

export default Profile;
